import React from "react";

const Header = () => {
  return (
    <>
      <header className="header">
        <h1 className="logo">cyber chat</h1>
      </header>
    </>
  );
};

export const withHeader = (WrappedComponent) => {
  return class WithHeader extends React.Component {
    render() {
      return (
        <>
          <Header />
          <WrappedComponent {...this.props} />
        </>
      );
    }
  };
};
